.App {
  text-align: center;
  height:100vh;
  width:100vw;

  .App-header {
    
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;
  }
  #text{
    font-weight:500;
    font-family:'Raleway', sans-serif;
  }

 #quote-box{
  margin:100px 200px;
 
  
  background-color:white;
   color:black;
  border-radius: 5px;
  padding:20px;
  #author{
    text-align: end;
    font-weight:250;
  }
 
 
 
   .buttons{
    
    display:flex;
    flex-direction:row;
    justify-content: space-between;
    #tweet-quote{
      text-decoration: none;
      float:left;
      margin:4%;
      border-radius: 3px;
      border:none;
      color:white;
      padding:10px 15px;
    }
    #new-quote{
      font-size:16px;
      cursor: pointer;
      padding:10px 15px;
      border-radius: 3px;
      border:none;
      float:right;
      margin:4%;
      color:white
       }
      



   }

 }
 
 
}






